import axios from 'axios';

const dataportalInstance = axios.create({
	baseURL: process.env.REACT_APP_DATA_PORTAL_HOST,
});

//   
dataportalInstance.interceptors.request.use(
	(config) =>
		//     
		config,
	(error) =>
		//     
		Promise.reject(error)
);

//   
dataportalInstance.interceptors.response.use(
	(response) =>
		// 2xx        .
		//     
		response,
	(error) =>
		// 2xx         .
		//     
		Promise.reject(error)
);

export default dataportalInstance;
